import React, { useState } from "react";
import { Form, Button, Modal } from "react-bootstrap";
import * as Yup from "yup";
import { useFormik } from "formik";
import config from "../../../config/config";
import { useSelector } from "react-redux";

export const ForgottenPassword = (props) => {
  const { user } = useSelector((state) => state.auth);

  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("");

  const schema = Yup.object({
    email: Yup.string()
      .email("Inserire una email valida")
      .required("Inserire la mail di login per recuperare la password"),
  });

  const formik = useFormik({
    initialValues: {
      email: "",
    },
    validationSchema: schema,
    enableReinitialize: true,
    onSubmit: (values) => {
      const requestOptions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
        body: JSON.stringify({
          fullname: user.fullname,
          email: user.emailAziendale === "undefined" ? user.email : user.emailAziendale,
          values: values,
        }),
      };
      setLoading(true);
      fetch(config.apiUrl + "users/ForgottenPassword.php", requestOptions)
        .then((response) => response.json())
        .then((data) => {
          setColor(data["color"]);
          setMessage(data["message"]);
        })
        .then(() => setLoading(false));
    },
  });

  const getInputClassesForHandle = (fieldname) => {
    if (formik.touched[fieldname] && formik.errors[fieldname]) {
      return "is-invalid";
    }

    if (formik.touched[fieldname] && !formik.errors[fieldname]) {
      return "is-valid";
    }
    return "";
  };

  return (
    <>
      <Modal
        show={props.show}
        onHide={props.handleClose}
        size="lg"
        aria-labelledby="cantained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton={true}>
          <Modal.Title id="contained-modal-title-vcenter">
            Recupero password
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {loading === false ? (
            <Form onSubmit={formik.handleSubmit}>
              <Form.Row>
                <Form.Group className="col-12">
                  <Form.Label className="col-12">Email</Form.Label>
                  <Form.Control
                    type="text"
                    value={formik.values.email}
                    name="email"
                    onChange={formik.handleChange}
                    className={`form-control ${getInputClassesForHandle(
                      "email"
                    )}`}
                    placeholder="Inserisci email..."
                  />
                  {message === "" ? (
                    <label className="text-muted col-12 mt-4">
                      Inserire email utilizzata per il login, verr√† inviata una
                      mail di recupero
                    </label>
                  ) : (
                    <p style={{ color: color }}>{message}</p>
                  )}
                </Form.Group>
                {formik.touched.email && formik.errors.email ? (
                  <div className="fv-plugins-message-container">
                    <div className="fv-help-block">{formik.errors.email}</div>
                  </div>
                ) : null}
              </Form.Row>
            </Form>
          ) : (
            <div className="divLoading" style={{ padding: "50px" }}>
              <span
                className="spinner-border"
                style={
                  loading
                    ? { width: "6rem", height: "6rem" }
                    : { display: "none" }
                }
                role="status"
              >
                <span className="sr-only">Caricamento...</span>
              </span>
              <p className="my-7 mt-4">Recupero password in corso...</p>
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="primary"
            className="float-right"
            onClick={formik.handleSubmit}
          >
            Recupera
          </Button>
          <Button
            variant="secondary"
            className="float-right"
            onClick={props.handleClose}
          >
            Annulla
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};
