/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from "react";
import { useSelector, shallowEqual, connect } from "react-redux";
import { useFormik } from "formik";
import * as Yup from "yup";
import { ModalProgressBar } from "../../../_metronic/_partials/controls";
import * as auth from "../Auth";
import config from "../../../config/config";
import { ForgottenPassword } from './ForgottenPassword';

function ChangePassword(props) {
  // Fields
  const [loading, setloading] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("");
  const [show, setShow] = useState(false);
  const handleShow = () => setShow(true);
  const handleClose = () => setShow(false);
  const user = useSelector((state) => state.auth.user, shallowEqual);

  // UI Helpers
  const initialValues = {
    currentPassword: "",
    password: "",
    cPassword: "",
  };
  const Schema = Yup.object().shape({
    currentPassword: Yup.string().required("Inserisci la password attuale"),
    password: Yup.string().required("Inserisci una nuova password"),
    cPassword: Yup.string()
      .required("Devi confermare la nuova password")
      .when("password", {
        is: (val) => (val && val.length > 7 ? true : false),
        then: Yup.string().oneOf(
          [Yup.ref("password")],
          "Le due password non corrispondono"
        ),
      }),
  });
  const getInputClasses = (fieldname) => {
    if (formik.touched[fieldname] && formik.errors[fieldname]) {
      return "is-invalid";
    }

    if (formik.touched[fieldname] && !formik.errors[fieldname]) {
      return "is-valid";
    }

    return "";
  };
  const formik = useFormik({
    initialValues,
    validationSchema: Schema,
    onSubmit: (values) => {
      const requestOptions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
        body: JSON.stringify({
          fullname: user.fullname,
          email: user.emailAziendale === "undefined" ? user.email : user.emailAziendale,
          idUser: user.id,
          values: values,
        }),
      };
      setloading(true);
      fetch(config.apiUrl + "users/UpdatePassword.php", requestOptions)
        .then((response) => response.json())
        .then((data) => {
          setMessage(data["message"]);
          setColor(data["color"]);
        })
        .then(() => setloading(false));
    },
    onReset: (values, { resetForm }) => {
      resetForm();
    },
  });

  return (
    <>
    <ForgottenPassword 
      show = {show}
      handleShow = {handleShow}
      handleClose = {handleClose}
    />

    <form className="card card-custom" onSubmit={formik.handleSubmit}>
      {loading && <ModalProgressBar />}

      {/* begin::Header */}
      <div className="card-dashboard py-3">
        <div className="card-title align-items-start flex-column">
          <h3 className="card-label font-weight-bolder text-dark">
            Cambia Password
          </h3>
          <span className="text-muted font-weight-bold font-size-sm mt-1">
            Cambia la password del tuo account
          </span>
        </div>
      </div>
      {/* end::Header */}
      {/* begin::Form */}
      <div className="form">
        <div className="card-body">
          <div className="form-group row">
            <label className="col-xl-3 col-lg-3 col-form-label text-alert">
              Password corrente
            </label>
            <div className="col-lg-9 col-xl-6">
              <input
                type="password"
                placeholder="Password corrente"
                className={`form-control form-control-lg form-control-solid mb-2 ${getInputClasses(
                  "currentPassword"
                )}`}
                name="currentPassword"
                {...formik.getFieldProps("currentPassword")}
              />
              {message === "" ? null : (
                <p style={{ color: color }}>{message}</p>
              )}
              {formik.touched.currentPassword &&
              formik.errors.currentPassword ? (
                <div className="invalid-feedback">
                  {formik.errors.currentPassword}
                </div>
              ) : null}
              <a onClick={handleShow} className="text-sm font-weight-bold">
                Hai dimenticato la password ?
              </a>
            </div>
          </div>
          <div className="form-group row">
            <label className="col-xl-3 col-lg-3 col-form-label text-alert">
              Nuova password
            </label>
            <div className="col-lg-9 col-xl-6">
              <input
                type="password"
                placeholder="Nuova password"
                className={`form-control form-control-lg form-control-solid ${getInputClasses(
                  "password"
                )}`}
                name="password"
                {...formik.getFieldProps("password")}
              />
              {formik.touched.password && formik.errors.password ? (
                <div className="invalid-feedback">{formik.errors.password}</div>
              ) : null}
            </div>
          </div>
          <div className="form-group row">
            <label className="col-xl-3 col-lg-3 col-form-label text-alert">
              Verifica la password
            </label>
            <div className="col-lg-9 col-xl-6">
              <input
                type="password"
                placeholder="Verifica la password"
                className={`form-control form-control-lg form-control-solid ${getInputClasses(
                  "cPassword"
                )}`}
                name="cPassword"
                {...formik.getFieldProps("cPassword")}
              />
              {formik.touched.cPassword && formik.errors.cPassword ? (
                <div className="invalid-feedback">
                  {formik.errors.cPassword}
                </div>
              ) : null}
            </div>
          </div>
          <div className="card-toolbar">
            <button
              type="submit"
              className="btn btn-success my-5  float-right"
              disabled={
                (formik.touched && !formik.isValid)
              }
            >
              Conferma
              {formik.handleSubmit}
            </button>
          </div>
        </div>
      </div>
      {/* end::Form */}
    </form>
    </>
  );
}

export default connect(null, auth.actions)(ChangePassword);
