import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { useFormik } from "formik";
import * as Yup from "yup";
import config from "../../../config/config";
import { Form, Button } from "react-bootstrap";
import "../../../style.css";
import { Skeleton } from "@material-ui/lab";
import Select from "react-select";

export default function PersonaInformation(props) {
  /*eslint-disable */
  const user = useSelector((state) => state.auth.user);
  // Fields
  const [loading, setloading] = useState(false);
  const [pic, setPic] = useState();

  const [countryUser, setCountryUser] = useState({ label: "", value: "" });
  const [regionUser, setRegionUser] = useState({ label: "", value: "" });
  const [provinceUser, setProvinceUser] = useState({ label: "", value: "" });
  const [cityUser, setCityUser] = useState({ label: "", value: "" });

  //STATO PER GESTIRE LA SELEZIONE DI NAZIONE, REGIONE, CITT√Ä
  const [province, setProvince] = useState([]);
  const [country, setCountry] = useState([]);
  const [regione, setRegione] = useState([]);
  const [city, setCity] = useState([]);
  const [phoneMessage, setPhoneMessage] = useState("");
  const [emailMessage, setEmailMessage] = useState("");
  const [emailAziendaleMessage, setEmailAziendaleMessage] = useState("");
  const [messagePhone, setMessagePhone] = useState(false);
  const [messageEmail, setMessageEmail] = useState(false);
  const [messageEmailAziendale, setMessageEmailAziendale] = useState(false);
  const [result, setResult] = useState(false);

  const [selectedCountry, setSelectedCountry] = useState(0);
  const [selectedRegion, setSelectedRegion] = useState(0);
  const [selectedProvince, setSelectedProvince] = useState(0);
  const [selectedCity, setSelectedCity] = useState(0);

  const GetCountry = async () => {
    const requestOptions = {
      headers: {
        Authorization:
          "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
      },
      method: "POST",
    };

    var rawData = await fetch(
      config.apiUrl + "geolocal/GetCountries.php",
      requestOptions
    );
    var data = await rawData.json();
    setCountry(data);
    setCountryUser(data.find((o) => o.label === user.country));
  };

  const GetProvince = async () => {
    const requestOptions = {
      headers: {
        Authorization:
          "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
      },
      method: "POST",
      body: JSON.stringify({
        idRegion: regionUser.value,
      }),
    };

    var rawData = await fetch(
      config.apiUrl + "geolocal/GetProvinces.php",
      requestOptions
    );
    var data = await rawData.json();
    setProvince(data);
    setProvinceUser(data.find((o) => o.label === user.province));
  };

  const GetCity = async () => {
    const requestOptions = {
      headers: {
        Authorization:
          "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
      },
      method: "POST",
      body: JSON.stringify({
        idProvince: provinceUser.value,
      }),
    };
    var rawData = await fetch(
      config.apiUrl + "geolocal/GetCities.php",
      requestOptions
    );
    var data = await rawData.json();
    setCity(data);
    setCityUser(data.find((o) => o.label === user.city));
  };

  const GetRegione = async () => {
    const requestOptions = {
      headers: {
        Authorization:
          "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
      },
      method: "POST",
      body: JSON.stringify({}),
    };
    var rawData = await fetch(
      config.apiUrl + "geolocal/GetRegions.php",
      requestOptions
    );
    var data = await rawData.json();
    setRegione(data);
    setRegionUser(data.find((o) => o.label === user.region));
  };

  const Schema = Yup.object().shape({
    firstname: Yup.string()
      .required("Campo richiesto")
      .matches(
        /^[aA-zZ\s]+$/,
        "Non sono ammessi caratteri numerici per questo campo"
      ),
    lastname: Yup.string()
      .required("Campo richiesto")
      .matches(
        /^[aA-zZ\s]+$/,
        "Non sono ammessi caratteri numerici per questo campo"
      ),
    companyName: Yup.string()
      .notRequired()
      .min(3, "Minimo 3 caratteri")
      .max(50, "Massimo 50 caratteri"),
    phone: Yup.string().required("Campo richiesto"),
    email: Yup.string()
      .email("Inserire una email valida")
      .required("Campo richiesto"),
    vatNumber: Yup.string("Inserire una partita IVA valida").notRequired(),
  });

  const getInputClasses = (fieldname) => {
    if (formik.touched[fieldname] && formik.errors[fieldname]) {
      return "is-invalid";
    }

    if (formik.touched[fieldname] && !formik.errors[fieldname]) {
      return "is-valid";
    }

    return "";
  };

  const formik = useFormik({
    initialValues: {
      firstname: user.firstname,
      lastname: user.lastname,
      moduleName: user.moduleName,
      department: user.nameDepartment,
      ssn: user.ssn === "undefined" || user.ssn === "null" ? "" : user.ssn,
      companyName: user.companyName,
      phone: user.phone,
      email: user.email,
      emailAziendale:
        user.emailAziendale === "undefined" ? "" : user.emailAziendale,
      zip_code: user.zip_code,
      birthday: user.birthday,
      region: regionUser !== undefined ? regionUser.label : "",
      country: countryUser !== undefined ? countryUser.label : "",
      province: provinceUser !== undefined ? provinceUser.label : "",
      vatNumber: user.vat_number,
      city: cityUser !== undefined ? cityUser.label : "",
      address: user.address,
      iban: user.iban,
    },
    validationSchema: Schema,
    onSubmit: (values) => {
      const formResult = new FormData();
      for (let value in values) {
        if (countryUser !== "Italia") {
          formResult.append(value, values[value]);
        } else {
          if (
            values[value] !== formik.values.province ||
            values[value] !== formik.values.country ||
            values[value] !== formik.values.city ||
            values[value] !== formik.values.region
          ) {
            formResult.append(value, values[value]);
          }
        }
      }
      formResult.append("pic", pic);
      formResult.append("idUser", user.id);
      if (countryUser.label === "Italia") {
        formResult.append("country", countryUser.label);
        formResult.append("region", regionUser.label);
        formResult.append("province", provinceUser.label);
        formResult.append("city", cityUser.label);
      }
      const requestOptions = {
        headers: {
          Authorization:
            "271c4d716fcf0e9555b51cffed666b4321f98f7f8bbeb9ae8bfc67752b4db8a2",
        },
        method: "POST",
        body: formResult,
      };
      setloading(true);
      fetch(config.apiUrl + "users/UploadProfile.php", requestOptions)
        .then((response) => response.json())
        .then((data) => {
          setResult(data["result"]);
          setPhoneMessage(data["phone"]["message"]); //string
          setEmailMessage(data["email"]["message"]); //string
          setEmailAziendaleMessage(["emailAziendale"]["message"]); //string
          setMessagePhone(data["phone"]["result"]); //boolean
          setMessageEmail(data["email"]["result"]); //boolean
          setMessageEmailAziendale(data["emailAziendale"]["result"]); //boolean
        })
        .then(() => setloading(false));
      values.preventDefault();
    },
  });

  useEffect(() => {
    GetCountry();
  }, []);

  useEffect(() => {
    if (countryUser !== undefined) {
      if (countryUser.label === "Italia") {
        GetRegione();
      } else {
        setRegione([]);
      }
    }
  }, [countryUser]);

  useEffect(() => {
    if (regionUser !== undefined) {
      if (regionUser.value !== "") {
        GetProvince();
      }
    }
  }, [regionUser]);

  useEffect(() => {
    if (provinceUser !== undefined) {
      if (provinceUser.value !== "") {
        GetCity();
      }
    }
  }, [provinceUser]);

  return (
    <>
      <form
        className="card card-custom card-stretch"
        onSubmit={formik.handleSubmit}
      >
        {/* begin::Header */}
        <div className="card-dashboard py-3">
          <div className="card-title align-items-start flex-column">
            <h3 className="card-label font-weight-bolder text-dark">
              Informazioni personali
            </h3>
            <span className="text-muted font-weight-bold font-size-sm mt-1">
              Aggiorna le tue informazioni personali
            </span>
          </div>
        </div>
        {/* end::Header */}
        {/* begin::Form */}
        <div className="form">
          {/* begin::Body */}
          <div className="card-body">
            <div className="row">
              <div className="row">
                <h6 className="col-12 mb-6">Anagrafica</h6>

                <Form.Group className="col-12">
                  <Form.Label>Immagine profilo</Form.Label>
                  {!loading ? (
                    <div className="custom-file">
                      <Form.File
                        type="file"
                        accept="image/jpeg, image/png, image/jpg"
                        className="custom-file-input"
                        id="customFile"
                        style={{ visibility: "hidden" }}
                        onChange={(e) => setPic(e.target.files[0])}
                      />
                      <label
                        className="col-6 custom-file-label"
                        htmlFor="customFile"
                      >
                        {pic ? pic.name : "Seleziona file: jpg, jpeg, png"}
                      </label>
                    </div>
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                </Form.Group>

                <Form.Group className="col-6">
                  <Form.Label className="col-12">Nome</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="firstname"
                      className={getInputClasses("firstname")}
                      {...formik.getFieldProps("firstname")}
                      value={formik.values.firstname}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.firstname && formik.errors.firstname ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.firstname}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Cognome</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="lastname"
                      className={getInputClasses("lastname")}
                      {...formik.getFieldProps("lastname")}
                      value={formik.values.lastname}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.lastname && formik.errors.lastname ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.lastname}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Email</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="email"
                      className={getInputClasses("email")}
                      {...formik.getFieldProps("email")}
                      value={formik.values.email}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {messageEmail ? (
                    <p style={{ color: "red" }}>{emailMessage}</p>
                  ) : null}
                  {formik.touched.email && formik.errors.email ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.email}</div>
                    </div>
                  ) : null}
                  <Form.Label className="text-muted">
                    Email utilizzata per login
                  </Form.Label>
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Email aziendale</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="emailAziendale"
                      className={getInputClasses("emailAziendale")}
                      {...formik.getFieldProps("emailAziendale")}
                      value={formik.values.emailAziendale}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {messageEmailAziendale ? (
                    <p style={{ color: "red" }}>{emailAziendaleMessage}</p>
                  ) : null}
                  {formik.touched.emailAziendale &&
                  formik.errors.emailAziendale ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.emailAziendale}
                      </div>
                    </div>
                  ) : null}
                  <Form.Label className="text-muted">
                    Se presente verr√† utilizzata per inviare notifiche
                  </Form.Label>
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Codice fiscale</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="ssn"
                      className={getInputClasses("ssn")}
                      {...formik.getFieldProps("ssn")}
                      value={formik.values.ssn}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.ssn && formik.errors.ssn ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.ssn}</div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Data di nascita</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="date"
                      name="birthday"
                      className={getInputClasses("birthday")}
                      {...formik.getFieldProps("birthday")}
                      value={formik.values.birthday}
                      onChange={formik.handleChange}
                      required
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.birthday && formik.errors.birthday ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.birthday}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
                <h6 className="col-12 my-6">Informazioni personali</h6>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Numero telefono</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="phone"
                      className={getInputClasses("phone")}
                      {...formik.getFieldProps("phone")}
                      value={formik.values.phone}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {messagePhone ? (
                    <p style={{ color: "red" }}>{phoneMessage}</p>
                  ) : null}
                  {formik.touched.phone && formik.errors.phone ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.phone}</div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">IBAN</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="iban"
                      className={getInputClasses("iban")}
                      {...formik.getFieldProps("iban")}
                      value={formik.values.iban}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.iban && formik.errors.iban ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">{formik.errors.iban}</div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Nazione</Form.Label>
                  {!loading ? (
                    <Select
                      value={
                        countryUser !== undefined
                          ? {
                              value: countryUser.value,
                              label: countryUser.label,
                            }
                          : ""
                      }
                      options={country}
                      onChange={(e) => setCountryUser(e)}
                      placeholder="Seleziona nazione..."
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                </Form.Group>
                {countryUser.label === "Italia" ? (
                  <>
                    <Form.Group className="col-6">
                      <Form.Label className="col-12">Regione</Form.Label>
                      {!loading ? (
                        <Select
                          value={
                            regionUser !== undefined
                              ? {
                                  value: regionUser.value,
                                  label: regionUser.label,
                                }
                              : ""
                          }
                          options={regione}
                          onChange={(e) => setRegionUser(e)}
                          placeholder="Seleziona regione..."
                        />
                      ) : (
                        <Skeleton animation="wave" variant="text" />
                      )}
                    </Form.Group>
                    <Form.Group className="col-6">
                      <Form.Label className="col-12">Provincia</Form.Label>
                      {!loading ? (
                        <Select
                          value={
                            provinceUser !== undefined
                              ? {
                                  value: provinceUser.value,
                                  label: provinceUser.label,
                                }
                              : ""
                          }
                          options={province}
                          onChange={(e) => setProvinceUser(e)}
                          placeholder="Seleziona provincia..."
                        />
                      ) : (
                        <Skeleton animation="wave" variant="text" />
                      )}
                    </Form.Group>
                    <Form.Group className="col-6">
                      <Form.Label className="col-12">Comune</Form.Label>
                      {!loading ? (
                        <Select
                          value={
                            cityUser !== undefined
                              ? {
                                  value: cityUser.value,
                                  label: cityUser.label,
                                }
                              : ""
                          }
                          options={city}
                          onChange={(e) => setCityUser(e)}
                          placeholder="Seleziona comune..."
                        />
                      ) : (
                        <Skeleton animation="wave" variant="text" />
                      )}
                    </Form.Group>
                  </>
                ) : (
                  <Form.Group className="col-6">
                    <Form.Label className="col-6">Citt√†</Form.Label>
                    {!loading ? (
                      <Form.Control
                        type="text"
                        name="city"
                        className={getInputClasses("city")}
                        {...formik.getFieldProps("city")}
                        value={formik.values.city}
                        onChange={formik.handleChange}
                      />
                    ) : (
                      <Skeleton animation="wave" variant="text" />
                    )}
                    {formik.touched.city && formik.errors.city ? (
                      <div className="fv-plugins-message-container">
                        <div className="fv-help-block">
                          {formik.errors.city}
                        </div>
                      </div>
                    ) : null}
                  </Form.Group>
                )}
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Indirizzo</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="address"
                      className={getInputClasses("address")}
                      {...formik.getFieldProps("address")}
                      value={formik.values.address}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.address && formik.errors.address ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.address}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">CAP</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="zip_code"
                      className={getInputClasses("zip_code")}
                      {...formik.getFieldProps("zip_code")}
                      value={formik.values.zip_code}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.zip_code && formik.errors.zip_code ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.zip_code}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
                <h6 className="col-12 my-6">Informazioni professionali</h6>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Partita IVA</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="vatNumber"
                      className={getInputClasses("vatNumber")}
                      value={formik.values.vatNumber}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.vatNumber && formik.errors.vatNumber ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.vatNumber}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Nome compagnia</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="companyName"
                      className={getInputClasses("companyName")}
                      {...formik.getFieldProps("companyName")}
                      value={formik.values.companyName}
                      onChange={formik.handleChange}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.companyName && formik.errors.companyName ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.companyName}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Settore</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="moduleName"
                      className={getInputClasses("moduleName")}
                      value={formik.values.moduleName}
                      onChange={formik.handleChange}
                      {...formik.getFieldProps("moduleName")}
                      disabled={true}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.moduleName && formik.errors.moduleName ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.moduleName}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
                <Form.Group className="col-6">
                  <Form.Label className="col-12">Dipartimento</Form.Label>
                  {!loading ? (
                    <Form.Control
                      type="text"
                      name="department"
                      className={getInputClasses("department")}
                      value={formik.values.department}
                      onChange={formik.handleChange}
                      {...formik.getFieldProps("department")}
                      disabled={true}
                    />
                  ) : (
                    <Skeleton animation="wave" variant="text" />
                  )}
                  {formik.touched.department && formik.errors.department ? (
                    <div className="fv-plugins-message-container">
                      <div className="fv-help-block">
                        {formik.errors.department}
                      </div>
                    </div>
                  ) : null}
                </Form.Group>
              </div>
            </div>
          </div>
          {/* end::Body */}
          {result ? (
            <p className="float-right col-12" style={{ color: "green" }}>
              Profilo modificato con successo!
            </p>
          ) : null}
          <Button
            variant="primary"
            size="sm"
            className="float-right mb-5 mr-5"
            onClick={formik.handleSubmit}
          >
            Aggiorna profilo
          </Button>
        </div>

        {/* end::Form */}
      </form>
    </>
  );
}
